import './App.css';
import { MyHand } from './MyHand.js';
import { CardTable } from './CardTable.js';
import { useState, useEffect, useCallback } from 'react';
import * as CardUtils from './card-utils';

function App() {

  console.log("App called")

  const [playerInputs,setPlayerInput] = useState({
    player1: "",
    player2: "",
    player3: "",
    player4: ""
  })
  const [players,setPlayers] = useState([])
  const [restoreCard,setRestoreCard] = useState()
  const [log,setLog] = useState("")
  const [hand,setHand] = useState([])
  const [player,setPlayer] = useState('...')
  const [ws,setWs] = useState()
  const [game,setGame] = useState(resetGame())
  
  function resetGame() { return {
    "hearts": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ],
    "clubs": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ],
    "diamonds": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ],
    "spades": [
        { value: 'A', played : false },
        { value: '2', played : false },
        { value: '3', played : false },
        { value: '4', played : false },
        { value: '5', played : false },
        { value: '6', played : false },
        { value: '7', played : false },
        { value: '8', played : false },
        { value: '9', played : false },
        { value: '10', played : false },
        { value: 'J', played : false },
        { value: 'Q', played : false },
        { value: 'K', played : false }
      ]
    }
  }

  const getNextPlayer = useCallback(
    (p) => {
      let index = players.findIndex(plyr => plyr === p) + 1

      index = (index >= players.length) ? 0 : index;
  
      return (players[index])
      },
    [players],
  );


  useEffect( () => {

    function getCard(card) {
      let value = CardUtils.getCardValue(card)
      let suit = CardUtils.getSuitSymbol(card)
      return (value + suit)
    }

    function updateHand(newhand) {
      console.log(`Update hand called with ${newhand}`)
  
      newhand.sort( (a,b) => {
        let ai = CardUtils.cardpack.findIndex(c => a === c)
        let bi = CardUtils.cardpack.findIndex(c => b === c)
        return (ai - bi)
      })
  
      setHand(newhand)
    }

    function newGame() {
      setLog(`New game!.... please rejoin to get new hand`)
      setPlayer('...')
      setGame(resetGame())
      setHand([])
    }  
  
    function getWs() {
      var HOST = "wss://jbsevens.herokuapp.com"
      let localWs = new WebSocket(HOST);
      console.log("creating connection!")
      localWs.onmessage = (event) => {
        console.log(event.data)
        let m = JSON.parse(event.data);

        switch(m.type) {
          case 'card':
            setLog(`${m.player} played ${getCard(m.card)} and has ${m.left} card(s) left - ${getNextPlayer(m.player)}'s go...`)
            updateGame(m.card,'play');
            break;
          case 'knock':
            setLog(`${m.player} knocked! - ${getNextPlayer(m.player)}'s go...`)
            break;
          case 'retractCard':
            let value = CardUtils.getCardValue(m.card)
            let suit = CardUtils.getSuitSymbol(m.card)
            setLog(`${m.player} retracted card ${value + suit}`)
            updateGame(m.card,'retract')
            break;
          case 'message':
            setLog(`${m.message}`)
            break;
          case 'players':
            setPlayers(m.players)
            break;
          case 'newgame':
            newGame();
            break;
          case 'hand':
            updateHand(m.cards);
            break;
          default:
            console.log("unsupported message type")
        }
      }
      setInterval( () => {
        localWs.send(
          JSON.stringify({
            type: 'ping'
          })          
        )
      },30000)
      return localWs
    }
  
    setWs(getWs())
  },[getNextPlayer])

  const sendCard = (card,left) => {
    ws.send(JSON.stringify({
      type: 'card',
      player: player,
      left: left,
      card: card
    }))
  }

  const newgame = () => {
    ws.send(JSON.stringify({
      type: 'newgame'
    }))  
  }

  const knock = () => {
    ws.send(JSON.stringify({
      type: 'knock',
      player: player
    }))
  }

  const removeCard = (card) => {
    if(window.confirm(`Are you sure you want to retract the ${card}?`)){ 
      setRestoreCard(card)
      ws.send(JSON.stringify({
        type: 'retractCard',
        player: player,
        card: card
      }))    
    }
  }

  function updatePlayer(evt) {

    console.log(`update player called for: ${evt.target.value}`)

    let p = evt.target.value
    sendHandRequest(p)
    setPlayer(p)
  }

  function updateGame(card,action) {
    setGame( g => {

      let suit = card.length === 2 ? card.charAt(1) : card.charAt(2);
      let cardValue = card.length === 2 ? card.substring(0,1) : card.substring(0,2)

      let played = true;

      if (action === 'play') {
        played = true 
      } else if ( action === 'retract') {
        played = false
      }
  
      const newTable = {
        hearts: g["hearts"].map( oldcard => {
          if (suit === "H" && cardValue === oldcard.value) return { value: oldcard.value, played: played}
          else return { value: oldcard.value, played: oldcard.played}
        }),
        clubs: g["clubs"].map( oldcard => {
          if (suit === "C" && cardValue === oldcard.value) return { value: oldcard.value, played: played}
          else return { value: oldcard.value, played: oldcard.played}
        }),
        diamonds: g["diamonds"].map( oldcard => {
          if (suit === "D" && cardValue === oldcard.value) return { value: oldcard.value, played: played}
          else return { value: oldcard.value, played: oldcard.played}
        }),
        spades: g["spades"].map( oldcard => {
          if (suit === "S" && cardValue === oldcard.value) return { value: oldcard.value, played: played}
          else return { value: oldcard.value, played: oldcard.played}
        })
      }
    
      return(newTable)    
    })
  }
  
  function sendHandRequest(p) {
    console.log(`send hand request called, player: ${p}`)
    if (p) {
      ws.send(JSON.stringify({
        type: 'hand',
        player: p
      }))  
    }
  }

  function initSession() {
    alert('init')
  }

  function addPlayers() {
    console.log(`Sending players: ${JSON.stringify(playerInputs)}`)
    ws.send(JSON.stringify({
      type: 'players',
      players: [
        playerInputs.player1,
        playerInputs.player2,
        playerInputs.player3,
        playerInputs.player4
      ]
    }))
  }

  function playerChanged(evt) {

    setPlayerInput(pi => {
      pi[evt.target.name] = evt.target.value
      return pi
    })
  }
  
  return (
    <div className="App">
      <div className="app-controls-and-hand">
        <div className="app-controls">
          <div className="app-container">
            <div className="app-playerselecter">
              <div><input type="button" onClick={newgame} value="New Game" /></div>
              { players.length > 0 &&
                <div>
                  <label htmlFor="players">Choose a player:</label>
                  <select value={player} name="players" id="players" onChange={updatePlayer}>
                    <option value="...">...</option>
                    <option value={players[0]}>{players[0]}</option>
                    <option value={players[1]}>{players[1]}</option>
                    <option value={players[2]}>{players[2]}</option>
                    <option value={players[3]}>{players[3]}</option>
                  </select>
                </div>
              }
            </div>
          </div>
          <div>
            <div className="app-log">{log}</div>
          </div>
        </div>

        {players.length === 0 &&
          <div className="app-setplayers">
            <form>
              <label htmlFor="player1">Player 1:</label><input type="text" name="player1" onChange={playerChanged}></input><br />
              <label htmlFor="player2">Player 2:</label><input type="text" name="player2" onChange={playerChanged}></input><br />
              <label htmlFor="player3">Player 3:</label><input type="text" name="player3" onChange={playerChanged}></input><br />
              <label htmlFor="player4">Player 4:</label><input type="text" name="player4" onChange={playerChanged}></input><br />
              <input type="button" value="Save" onClick={addPlayers}></input>
            </form>
          </div>
        }
        <MyHand hand={hand} playCard={sendCard} knock={knock} restoreCard={restoreCard}/> 
      </div>
      <CardTable game={game} remove={removeCard}/>
    </div>
  );
}

export default App;
