import "./Card.css";
import * as CardUtils from './card-utils';

export function Card(props) {

  console.log("Cards")

  function getColour(value) {

    const colours = {
      'C': 'card-black',
      'D': 'card-red',
      'S': 'card-black',
      'H': 'card-red'
    }
    let suit = value.length === 2 ? value.charAt(1) : value.charAt(2);

    return colours[suit]
  }

  const colourClass = getColour(props.value)

  const selected = props.selected ? "card-selected" : ""

  const suitSymbol = CardUtils.getSuitSymbol(props.value)
  const cardValue = CardUtils.getCardValue(props.value)
  const cardString = cardValue + suitSymbol
  const cardClassHidden = props.hidden ? " card-hidden" : ""
  const cardClasses = "card-card " + selected + cardClassHidden
  const valueClasses = "card-value " + colourClass

  return (
    <div className={cardClasses} onClick={props.onClick}>
      <div className={valueClasses}>
        {cardString}
      </div>
    </div>
  )
}